.header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(72, 72, 72);
    min-height: 10vh;
    width: 100vw;
    color: white;
    position: fixed;
}

.navbar ul {
    width: 50%;
    margin-right: 1100px;
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: larger;
}

a {
    text-decoration: none;
    color: white;
    transition: 0.3s;
}

a:hover {
    text-shadow: 0px 0px 10px white;
    transition: 0.3s;
}

.home {
    display: flex;
    justify-content: center;
}

.banner-image {
    width: 100vw;
}

.footer {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    padding: 25px;
    background-color: rgb(72, 72, 72);
}

.shop {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 100px;
    background-color: white;
}

.item-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 50px;
}

.product-image {
    height: 25vh;
    transition: 0.3s;
}

.product-image:hover {
    filter: drop-shadow(10px 10px 10px black);
    transition: 0.3s;
}

.item-detail {
    background-color: white;
    display: grid;

}

.cart {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    list-style: none;
    padding-top: 150px;
    padding-bottom: 100px;
}

.product-image-small {
    height: 10vh;
    padding: 25px;
}

.cart-item {
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-checkout {
    padding: 10px;
    font-size: large;
    border-radius: 10px;
}

.cart p {
    font-size: larger;
}

.add-remove-buttons {
    margin-left: 25px;
}

.add-remove-buttons button {
    margin-right: 5px;
}

@media (max-width: 1350px) {
    .header {
        display: none
    }
}